import { RenderToolbar, ItemKey } from '@ali/4ever-component';
import createClearStyleButton from './components/next/createClearStyleButton';

export default function createRenderToolbar(
  locale: Record<string, string>,
): RenderToolbar {

  return (props, controller) => {

    const { buttonName, extraProps } = props;

    if (buttonName === ItemKey.clearStyle) {
      return createClearStyleButton({
        controller,
        locale,
        ...extraProps,
      })
    }

    return null
  };
}
